import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { ByProductShopeeComponent } from './by-product-shopee.component';
import { RouterModule, Routes } from '@angular/router';
import { FormsModule } from '@angular/forms';
import { IonicModule } from '@ionic/angular';
import { TranslateModule } from '@ngx-translate/core';
import { NgZorroAntModule } from 'src/app/ng-zorro-ant.module';
const routes: Routes = [
  {
    path: '',
    component: ByProductShopeeComponent
  },
]

@NgModule({
  declarations: [ByProductShopeeComponent],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    NgZorroAntModule,
    TranslateModule,
    IonicModule,
    FormsModule
  ]
})
export class ByProductShopeeModule { }
